import { css, modifier, styled } from '@lokalise/styled';

import { spacing, value } from '../../../utils/theme';

export type MenuDrawerProps = {
	/**
	 * Toggles full width rendering of the MenuDrawer.
	 */
	$fullWidth?: boolean;
};

export const MenuDrawer = styled.div<MenuDrawerProps>`
	background-color: ${value('color.background.surface.default')};
	padding: ${spacing(1, 0)};
	width: 100%;
	border-radius: ${value('radius.dropdown')};
	border: 1px solid ${value('color.border.default')};
	box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
	z-index: ${value('zIndex.popover')};

	${modifier(
		'$fullWidth',
		css`
			width: 100%;
		`,
	)}
`;

MenuDrawer.displayName = 'MenuDrawer';
